@import "https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400&family=Montserrat:wght@200;400;600;800&display=swap";
html, body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
  overflow-x: hidden;
}

div#main {
  width: 100vw;
  flex-direction: column;
  display: flex;
}

section {
  width: 100vw;
  position: relative;
}

section.min-height {
  min-height: 100vh;
}

section.padding {
  padding: 5rem 0;
}

section img.background {
  height: 100%;
  object-fit: cover;
  pointer-events: none;
  width: 100vmax;
  z-index: -1;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%);
}

h1 {
  margin: 0;
  font-family: Cormorant Garamond, serif;
  font-size: 7rem;
  font-weight: 400;
  line-height: .8;
}

h2 {
  margin: 0;
  padding-top: 1rem;
  font-size: 2rem;
  font-weight: 400;
}

h3 {
  margin: 0;
  font-size: 1.25rem;
  font-weight: 400;
}

p {
  margin-bottom: 0;
  font-size: 18px;
  font-weight: 300;
  line-height: 1.5;
}

p.large {
  font-size: 24px;
}

p.small {
  font-size: 15px;
}

hr {
  margin: 2.5rem;
}

li {
  line-height: 1.7;
}

li::marker {
  color: #4e567e;
}

a {
  color: #000;
  text-decoration: none;
}

a:hover {
  color: #4e567e;
}

.container {
  grid-gap: 20px;
  grid-template-columns: 330px 300px;
  margin: 20px auto;
  display: grid;
}

.container .box {
  text-align: center;
  border: 1px solid #ccc;
  border-radius: 10px;
  flex-direction: column;
  justify-content: flex-start;
  padding: 24px 16px;
  display: flex;
}

img.socialIcon {
  height: 35px;
  width: 35px;
  filter: brightness(0) invert();
  transition: transform .2s;
}

img.socialIcon:hover {
  transform: scale(1.1);
}

.home-container {
  width: 100%;
  height: 100vh;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.background {
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
  position: absolute;
}

.home-text {
  color: #fff;
  max-width: 90%;
  background: #0009;
  border-radius: 10px;
  padding: 20px;
}

.home-text h1 {
  margin-bottom: .5rem;
  font-size: 3rem;
  font-weight: bold;
}

.home-text h2 {
  font-size: 1.5rem;
  font-weight: lighter;
}

.home-arrow {
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
}

.home-arrow img {
  height: 3rem;
  width: 3rem;
  animation: bounce 2s infinite;
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-10px);
  }
}

.blog-card {
  transition: all .3s;
}

.blog-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 25px #00000026;
}

a:focus {
  outline-offset: 2px;
  outline: 2px solid #4e567e;
}

@media (max-width: 768px) {
  .container {
    grid-template-columns: 1fr;
    margin: 20px;
  }

  .home-text h1 {
    font-size: 2rem;
  }

  .home-text h2 {
    font-size: 1.2rem;
  }

  section.padding {
    padding: 3rem 0;
  }
}

html {
  scroll-behavior: smooth;
}

.box {
  transition: transform .2s;
}

.box:hover {
  transform: translateY(-2px);
}

a {
  transition: color .2s;
}

